import React from 'react';
import { SizeMe } from 'react-sizeme'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Container from '../../../components/Container'

import useCSV from '../../../utils/useCSV'

import bg from './bg.svg'

const Page = ({ resizeRatio }) => {
  return (
    <Box height="100vh" position="relative" bg="orange">
      <Container position="relative">
        <Box position="absolute" left="0" right="0" top="0" bottom="0">
          <Image data-src={bg} />
        </Box>
        <Box position="absolute" width={511 * resizeRatio} left={223 * resizeRatio} top={151 * resizeRatio}>
          <Text.h2 color="yellow">觀眾支持行為剖析<Text.inline fontSize="1.25rem" color="white">（年平均）</Text.inline></Text.h2>
        </Box>
        <Box position="absolute" textAlign="center" lineHeight="1.25" color="white" left={197 * resizeRatio} top={423 * resizeRatio}>
          <Text.h2 fontSize="4.5rem" color="yellow">53%</Text.h2>
          <Text.h4 fontSize="1.5rem">購票張數</Text.h4>
          <Text.h4 fontSize="1.5rem">超過<Text.inline fontSize="2rem" color="white">2</Text.inline>張</Text.h4>
        </Box>
        <Box position="absolute" textAlign="center" lineHeight="1.25" color="white" left={503 * resizeRatio} top={400 * resizeRatio}>
          <Text.h2 fontSize="4.5rem" color="yellow">35%</Text.h2>
          <Text.h4 fontSize="1.5rem">會員購票</Text.h4>
          <Text.h4 fontSize="1.5rem">超過<Text.inline fontSize="2rem" color="white">1</Text.inline>張</Text.h4>
        </Box>
        <Box position="absolute" width={203 * resizeRatio} textAlign="center" lineHeight="1.25" color="white" left={800 * resizeRatio} top={405 * resizeRatio}>
          <Text.h2 fontSize="4.5rem" color="yellow">88%</Text.h2>
          <Text.h4 fontSize="1.5rem">偏好欣賞單一的演出類型</Text.h4>
          <Text fontSize="1em" mt="0.5em">（如：只買戲劇或音樂）</Text>
        </Box>
      </Container>
    </Box>
  );
};

export default Page;
