import React from 'react';
import { MdFileDownload } from 'react-icons/md';

import Box from '../../../components/Box'
import Flex from '../../../components/Flex'
import Text from '../../../components/Text'
import Image from '../../../components/Image'
import Button from '../../../components/Button'
import Container from '../../../components/Container'
import DotsBg from '../../../components/DotsBg'

import bglogo from './bglogo.svg'

const Page = ({ resizeRatio }) => {
  return (
    <DotsBg height="100vh">
      <Box pt="2.5em" position="relative" height="100%">
        <Box position="absolute" left="0" right="0" top="0.5em">
          <Image src={bglogo} />
        </Box>
        <Box position="absolute" top="190px" left="50%" transform="translateX(-50%)">
          <Text.h2 fontSize="2.25rem">THANK YOU</Text.h2>
        </Box>
        <Box position="absolute" top="347px" left="50%" transform="translateX(-50%)">
          <Text.h2 fontSize="2.625rem" color="yellow">讓更多人遇見表演藝術的美好</Text.h2>
        </Box>
        <Box textAlign="center" color="white" fontSize="1.5rem" position="absolute" top="410px" left="50%" transform="translateX(-50%)">
          <Text>透過數據持續關注產業的發展動向</Text>
          <Text>推動表演在全台各地持續開花</Text>
        </Box>
        <Box whiteSpace="pre-wrap" color="white" position="absolute" top="556px" left="50%" transform="translateX(-50%)">
          <Button px="2.5rem" py="1.375rem" textAlign="left" lineHeight="1.5" onClick={() => {}}>{`下載更完整的數據洞察\n《2013-2019年兩廳院售票系統消費行為報告`}</Button>
          <Flex mt="2em">
            <Box>與朋友分享此資訊</Box>
          </Flex>
        </Box>
      </Box>
    </DotsBg>
  );
};

export default Page;
